import React, { useContext, useEffect, useState } from "react";
import style from "./style";
import Box from "@mui/material/Box";
import useGoogleSheets from "use-google-sheets";
import Typography from "@mui/material/Typography";
import PredictedResult from "../../Components/PredictedResult";
import RefreshIcon from "@mui/icons-material/Refresh";
import axios from "axios";
import { fetchGoogleSheetsData } from "google-sheets-mapper";

export default function Home({
  firstTime,
  setFirstTime,
  lastRowNumber,
  setLastRowNumber,
}) {
  const [fatigueMonitor, setFatigueMonitor] = useState(null);
  const [spreadSheetData, setSpreadSheetData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const getData = async () => {
    setLoading(true);
    try {
      axios
        .get(
          `https://sheets.googleapis.com/v4/spreadsheets/1m5l9HZ4W9CZsqxf4RtI3Bl2BNiIhJu8dE9q5ydWbGkI/values:batchGet?ranges=Sheet1!A${
            lastRowNumber ? lastRowNumber : ""
          }:J${
            lastRowNumber ? lastRowNumber : ""
          }&key=AIzaSyBynmCXSa4nx_zsZXW3946QxDrsoylfahw`
        )
        .then((res) => {
          setLoading(false);
          if (firstTime) {
            setFirstTime(false);
            setLastRowNumber(res.data.valueRanges[0].values.length - 1);
            // console.log(
            //   "Res: ",
            //   res.data.valueRanges[0].values[
            //     res.data.valueRanges[0].values.length - 1
            //   ]
            // );
            setSpreadSheetData(
              res.data.valueRanges[0].values[
                res.data.valueRanges[0].values.length - 1
              ]
            );
            predictResult(
              res.data.valueRanges[0].values[
                res.data.valueRanges[0].values.length - 1
              ]
            );
          } else {
            if (res.data.valueRanges[0].values) {
              setLastRowNumber(lastRowNumber + 1);
              // console.log("Res: ", res.data.valueRanges[0].values[0]);
              setSpreadSheetData(res.data.valueRanges[0].values[0]);
              predictResult(res.data.valueRanges[0].values[0]);
            }
          }
        });
    } catch (error) {
      setLoading(false);
      console.error(error);
      setError(error);
    }
  };

  const predictResult = (docData) => {
    var data = {
      heartRate: docData[3],
      frequencyRoll: docData[5],
      frequencyPitch: docData[6],
    };

    console.log("Doc Data: ", data);
    var config = {
      method: "post",
      url: "http://139.59.255.63:8000/prediction/",
      headers: {
        "Content-Type": "application/json",
      },
      data: data,
    };
    axios(config)
      .then(function (response) {
        console.log(JSON.stringify(response.data));
        setFatigueMonitor(response.data.prediction.fatigue);
      })
      .catch(function (error) {
        console.log(error);
        setError(error);
      });
  };

  // console.log(loading, firstTime, lastRowNumber);

  useEffect(() => {
    getData();
    const docgetInterval = setInterval(() => {
      getData();
    }, 8000);
    // setFirstTime(false);
    return () => {
      clearInterval(docgetInterval);
    };
  }, []);

  // if (loading) {
  //   return (
  //     <Box
  //       sx={{
  //         display: "flex",
  //         justifyContent: "center",
  //         alignItems: "center",
  //       }}
  //     >
  //       <Typography>Loading...</Typography>
  //     </Box>
  //   );
  // }

  if (error) {
    return (
      <Box
        sx={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <Typography>Error!</Typography>
      </Box>
    );
  }


  console.log("lastRowNumber", spreadSheetData)
  return (
    <Box>
      <RefreshIcon
        onClick={() => getData()}
        sx={{ position: "absolute", fontSize: "50px", right: "0px", m: "30px" }}
      />
      <Box
        sx={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <Typography variant="h1">SHMS</Typography>
      </Box>
      <Box
        sx={{
          display: "flex",
          justifyContent: "center",
        }}
      >
        {spreadSheetData && (
          <PredictedResult
            data={spreadSheetData}
            fatigueMonitor={fatigueMonitor}
          />
        )}
      </Box>
    </Box>
  );
}
